.section {
  padding: 20px 0;

  @media screen and (min-width: $bp-tablet){
    padding: 48px 0;
  }

  &__title {
    font-size: 32px;
    font-weight: 600;
    line-height: 1.5;
    font-family: var(--second-family);

    @media screen and (min-width: $bp-tablet){
      font-size: 64px;
      line-height: 1.3;
    }

    &--centered {
      text-align: center;
    }
  }

  &__subtitle {
    font-family: var(--second-family);
    font-size: 30px;
    font-style: italic;
  }
}
